.card {
    background: #4db8ff;
    margin-left: 50px;
    border: 20px;
    border-color: black;
    border-radius: 10px;
    height: 200px;
    margin: 1rem;
    position: relative;
    width: 200px;
    text-align: left;
  }
  
  .card > .img-container {
    border-radius: 10px;
    height: 100%;
    overflow: hidden;
    text-align: center;
    background: #4db8ff;
  }
  
  .card > .img-container > img {
    width: 80%
  }
  
  .card > .img-container:hover {
    -webkit-transform: rotate(360deg);
        -ms-transform: rotate(360deg);
            transform: rotate(360deg);
    -webkit-transition: all 1s;
    -o-transition: all 1s;
    transition: all 1s;
    background: #005c99;

  }


